.form-row {
	padding: 12px;
	display: flex;
	justify-content: center;
}

form {
	border: 2px solid white;
	padding: 24px;
	font-size: 24px;
}

input {
	padding: 8px;
}

label,
input {
	margin: 16px;
	font-size: 24px;
}

form button {
	color: white;
	background-color: blue;
	border-radius: 40px;
	font-size: 22px;
	font-weight: 480;
}
